.popup__input{
    border: none;
    border-bottom: 1px solid rgba(0,0,0,0.5);
    font-weight: normal;
    font-size: 14px;
    line-height: 17px;
    color: #000000;
    padding-bottom: 10px;
    outline: none;
}

@media screen and (max-width : 425px){
    .popup__input{
        font-size: 3.29vw;
        line-height: 4vw;
    }
}